$bg-card-image: './scss/assets/images/Wood-Pattern5.png';

.flip-container {
  perspective: 1000px;
  display: inline-block;
  border: 1px solid #fff;
  // background-image: url(#{$bg-card-image});
  background-color: #000;
  &:hover {
    cursor: pointer;
  }
  &.flipped {
    .flipper {
      transform: rotateY(180deg);
    }
  }
  .flipper {
    transform-style: preserve-3d;
    position: relative;
    transition: 0.6s;
  }
  .front,
  .back {
    backface-visibility: hidden;
    position: absolute;
    left: 0;
    top: 0;
  }
  .back {
    z-index: 2;
    transform: rotateY(0deg);
  }
  .front {
    transform: rotateY(180deg);
  }
}
